import {Component, OnInit} from '@angular/core';
import {MapsService} from '../../services/maps.service';

@Component({
  selector: 'app-geoguessr',
  templateUrl: './geoguessr.component.html',
  styleUrls: ['./geoguessr.component.css']
})
export class GeoguessrComponent implements OnInit {
  gameMarker: any = {};
  gameSolution: any = {};
  timer = 0;
  isBR = true;
  interval: any;
  visitedCountries: any[] = [];

  constructor(private mapsService: MapsService) {
  }

  ngOnInit(): void {
    let ws = new WebSocket('ws://localhost:6306');
    ws.onclose = ev => {
      ws = null;
      console.log(ev);
    };
    ws.onopen = ev => {
      ws.send('over=');
    };
    ws.addEventListener('message', msg => {
      console.log(msg.data);
      const data = JSON.parse(msg.data);
      switch (data.state) {
        case 'start':
          clearInterval(this.interval);
          this.timer = 5;
          this.interval = setInterval(() => {
            --this.timer;
            if (!this.timer) {
              this.gameSolution = {};
            }
          }, 1000);
          break;
        case 'sol':
          this.gameSolution = data.rg[0];
          this.gameSolution.countryDetails = Object.values(this.gameSolution).splice(2, 4).filter(x => x).join(', ');
          this.visitedCountries.push(Object.assign({}, this.gameSolution));
          break;
        case 'geo':
          this.gameMarker = data.rg[0];
          this.gameMarker.countryDetails = Object.values(this.gameMarker).splice(2, 4).filter(x => x).join(', ');
          console.log(this.gameMarker);
          break;
      }
    });
  }

  emojiFlag(cc: string): string {
    return cc ? String.fromCodePoint(...[...cc.toUpperCase()].map(c => c.codePointAt(0) + 0x1F1E6 - 0x41)) : '';
  }

}
