import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HomeComponent} from './app/home/home.component';
import {ChatComponent} from './app/chat/chat.component';
import {ChatBraveComponent} from './app/chat-brave/chat-brave.component';
import {ChatBraveSmallComponent} from './app/chat-brave-small/chat-brave-small.component';
import {GeoguessrComponent} from './app/overlays/geoguessr/geoguessr.component';
import {IsaacComponent} from './app/overlays/isaac/isaac.component';
import {ZoomComponent} from './app/games/zoom/zoom.component';
import {FireworksComponent} from './app/fireworks/fireworks.component';

const routes: Routes = [
  {path: '**', component: ChatComponent},
  {path: 'home', component: HomeComponent},
  {path: 'chat', component: ChatComponent},
  {path: 'chat/brave', component: ChatBraveComponent},
  {path: 'chat/brave/small', component: ChatBraveSmallComponent},
  {path: 'overlays/geoguessr', component: GeoguessrComponent},
  {path: 'overlays/isaac', component: IsaacComponent},
  {path: 'overlays/fireworks', component: FireworksComponent},
  {path: 'tools/countdown', component: ZoomComponent}

];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
