import {Component, OnInit} from '@angular/core';
import {ChatComponent} from '../chat/chat.component';

@Component({
  selector: 'app-chat-brave-small',
  templateUrl: './chat-brave-small.component.html',
  styleUrls: ['./chat-brave-small.component.css']
})
export class ChatBraveSmallComponent extends ChatComponent implements OnInit {

  constructor() {
    super();
    this.IMAGE_MAX_RAND = 10;
  }

  ngOnInit(): void {
    super.ngOnInit();
    setInterval(el => {
      this.displayedMessages.splice(0, 1);
    }, 10000);
  }

  addMessage(tags, message): void {
    const msg = message.length > 100 ? message.substr(0, 100) + '...' : message;
    console.log(msg);
    const chatMessage = {
      author: tags['display-name'],
      color: tags.color,
      msg: this.setEmotes(tags, msg),
      randomNum: this.getRandomPkmn(tags)
    };
    if (this.displayedMessages.length > this.CHAT_MAX_MESSAGES - 1) {
      this.displayedMessages.splice(0, 1);
    }
    this.displayedMessages.push(chatMessage);
  }
}
