import {Component, OnInit} from '@angular/core';
import {Client} from 'tmi.js';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./css/chat.pmd.css']
})
export class ChatComponent implements OnInit {
  oauthToken: string;
  displayedMessages: any[] = [];
  chatType = 'pmd';

  CHAT_MAX_MESSAGES = 50;
  IMAGE_MAX_RAND = 129;

  constructor() {
  }

  ngOnInit(): void {
    const twitchClient = new Client({
      options: {debug: true, messagesLogLevel: 'info'},
      connection: {
        reconnect: true,
        secure: true
      },
      channels: ['ponce']
    });
    twitchClient.connect().catch(console.error);
    twitchClient.on('message', (channel, tags, message, self) => {
      if (self) {
        return;
      }
      console.log(tags);
      this.addMessage(tags, message);
    });

  }

  addMessage(tags, message): void {
    const chatMessage = {
      author: tags['display-name'],
      color: tags.color,
      msg: this.setEmotes(tags, message),
      randomNum: this.getRandomPkmn(tags)
    };
    if (this.displayedMessages.length > this.CHAT_MAX_MESSAGES - 1) {
      this.displayedMessages.splice(0, 1);
    }
    this.displayedMessages.push(chatMessage);
  }

  getRandomPkmn(tag): number {
    let num = 0;
    if (!tag.color) {
      num = [...tag['display-name']].reduce((a, c) => a + c.charCodeAt(0), 0) % this.IMAGE_MAX_RAND + 1;
    } else {
      num = [...tag.color].reduce((a, c) => a + c.charCodeAt(0), 0) % this.IMAGE_MAX_RAND + 1;
    }
    return num;
  }

  setEmotes(tag, message): string {
    const msg = [...message];
    if (tag.emotes) {
      Object.entries(tag.emotes).reverse().forEach((key: any[]) => {
        console.log(key);
        key[1].reverse().forEach(pos => {
          msg.splice(pos.split('-')[0], +pos.split('-')[1] - pos.split('-')[0] + 1, `<img src="https://static-cdn.jtvnw.net/emoticons/v2/${key[0]}/default/dark/2.0">`);
        });
      });
    }
    return msg.join('');
  }

}
