import {Component, OnInit} from '@angular/core';
import {Fireworks} from 'fireworks-js';
import * as Countdown from 'countdown';


@Component({
  selector: 'app-fireworks',
  templateUrl: './fireworks.component.html',
  styleUrls: ['./fireworks.component.css']
})
export class FireworksComponent implements OnInit {
  container: Element;
  fireworks: Fireworks;
  options: any = {
    hue: {
      min: 0,
      max: 345
    },
    delay: {
      min: 2,
      max: 34
    },
    rocketsPoint: 0,
    opacity: 0.5,
    speed: 17,
    acceleration: 1.2,
    friction: 1,
    gravity: 0.6000000000000001,
    particles: 657,
    trace: 7,
    explosion: 10,
    autoresize: true,
    brightness: {
      min: 50,
      max: 80,
      decay: {
        min: 0.015,
        max: 0.03
      }
    },
    boundaries: {
      x: 50,
      y: 50,
      width: 2560,
      height: 1297,
      visible: false
    },
    sound: {
      enabled: false,
      files: [
        'https://fireworks.js.org/sounds/explosion0.mp3',
        'https://fireworks.js.org/sounds/explosion1.mp3',
        'https://fireworks.js.org/sounds/explosion2.mp3'
      ],
      volume: {
        min: 2,
        max: 4
      }
    },
    mouse: {
      click: true,
      move: false,
      max: 1
    }
  };

  constructor() {
  }

  ngOnInit(): void {
    this.container = document.querySelector('.fireworks-container');
    this.fireworks = new Fireworks(this.container, this.options);
    setInterval(() => {
      if (new Date(2022, 0, 1).getTime() - Date.now() <= 0) {
        this.fireworks.start();
      }
    }, 1000);
  }

}
